#root {
    padding: 5px;
    display: grid;
    grid-gap: 7px;
    height: 100vh;
    grid-template-areas:"header header header header header"
                            "main main main main aside"
                            "main main main main aside"
                            "main main main main aside"
                        "footer footer footer footer footer";
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
}

.header {
    grid-area: header;
    align-items: center;
}

.header > div > h1 {
    padding-left: 20px;
}

.header,
.footer {
    border: 2px solid black;
    display: flex;
}

.header-title,
.footer-title {
    font-size: 36px;
}

.footer {
    grid-area: footer;
    align-items: center;
    justify-content: center;
}

.aside {
    grid-area: aside;
    padding: 23px 5px 0 23px;
    border: 2px solid black;
}

.aside-nav__list-elem > a {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 800;
    font-style: italic;
    color: #211212;
}

.aside-nav__list-elem:not(:last-child) {
    margin-bottom: 22px;
}


.main {
    grid-area: main;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
}

.main-inner {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.main-url {
    margin-bottom: 30px;
}

.main__subtitle {
    text-align: center;
    font-style: revert;
    font-weight: 500;
    font-size: 32px;
    margin-bottom: 22px;
}

.main__title {
    font-weight: 500;
    font-size: 56px;
    margin-bottom: 22px;
}